import React from 'react';
import SpoInfoItem from '../SpoInfoCard/SpoInfoItem'
import SpoInfoTitle from '../SpoInfoCard/SpoInfoTitle'
import * as nameTranslation from '../SpoServerInfo/NameTranslations';
import '../App/App.css';

const SpoInfoCard = (props) => {
    return(
        <div>
            <div className="card shadow mb-4">
                <div className="card-body">
                    <div className="col-lg-12">
                        <SpoInfoTitle icon={props.title.icon} title={nameTranslation[props.title.title]}/>
                        {
                            props.information.map((info) => {
                                return <SpoInfoItem text={info.text} value={info.value} />
                            })
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}
export default SpoInfoCard;