import React, { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom'
import Link from '@material-ui/core/Link';
import * as nameTranslation from '../SpoServerInfo/NameTranslations'
import * as linkLocations from '../SpoServerInfo/LinkLocations'
import axios from '../../sposerver_https'

const SpoInfoItemValue = ({
    linkLocation,
    value,
}) => {
    const [url, setUrl] = useState("")
    useEffect(() => {
        axios.get(`/page/indexInfo`).then(res => {
            setUrl(res.data.ethereumInfo.blockchainExplorerUrl)
        })
    })
    if(!linkLocation) {
        return <p className="text-truncate">{value}</p>
    } else if(value.length > 10) {
        const hrefValue = `${url}address/${value}`;
        return (
            <Link href = {hrefValue}>
                <p className="text-truncate">
                    {value}
                </p>
            </Link>
        )
    }else {
        return (
            <Link >
                <p className="text-truncate">
                    <RouterLink to={linkLocation} >{value}</RouterLink>
                </p>
            </Link>
        )
    }
}

const SpoInfoItem = (props) => {
    return(
        <div className="card-title-row">
            <div className="col-xl-6 col-lg-6 col-md-6">
                <h6 className="card-title text-left">
                    {nameTranslation[props.text]}:
                </h6>
            </div>
            <div className="col-xl-6 col-lg-6 col-md-6">
                <SpoInfoItemValue value={props.value} text={props.text} linkLocation={linkLocations[props.text]} />
            </div>
        </div>
    )
}
export default SpoInfoItem;