import axios from '../../sposerver_https'
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import React, { useState, useEffect } from 'react';
import IconButton from '@material-ui/core/IconButton';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import LastPageIcon from '@material-ui/icons/LastPage';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { Box } from '@material-ui/core';
import {timeStamp2String} from "../../../src/util";
import Datetime from 'react-datetime';
import '../App/react-datetime.css';
import moment from "moment";
import MultiSelect from "react-multi-select-component";
import _ from 'lodash';
import Alert from '@material-ui/lab/Alert';

const useStyles1 = makeStyles(theme => ({
    root: {
      flexShrink: 0,
      marginLeft: theme.spacing(2.5),
    },
    container: {
        maxHeight: 600
    },
    chips: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    chip: {
        margin: 2,
    },
}));

function TablePaginationActions(props) {
    const classes = useStyles1();
    const theme = useTheme();
    const { count, page, rowsPerPage, onChangePage } = props;
  
    const handleFirstPageButtonClick = event => {
        onChangePage(event, 0);
    };
  
    const handleBackButtonClick = event => {
        onChangePage(event, page - 1);
    };
  
    const handleNextButtonClick = event => {
        onChangePage(event, page + 1);
    };
  
    const handleLastPageButtonClick = event => {
        onChangePage(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
    };
  
    return (
      <div className={classes.root}>
        <IconButton
          onClick={handleFirstPageButtonClick}
          disabled={page === 0}
          aria-label="first page"
        >
          {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
        </IconButton>
        <IconButton onClick={handleBackButtonClick} disabled={page === 0} aria-label="previous page">
          {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
        </IconButton>
        <IconButton
          onClick={handleNextButtonClick}
          disabled={page >= Math.ceil(count / rowsPerPage) - 1}
          aria-label="next page"
        >
          {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
        </IconButton>
        <IconButton
          onClick={handleLastPageButtonClick}
          disabled={page >= Math.ceil(count / rowsPerPage) - 1}
          aria-label="last page"
        >
          {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
        </IconButton>
      </div>
    );
  }
  
TablePaginationActions.propTypes = {
    count: PropTypes.number.isRequired,
    onChangePage: PropTypes.func.isRequired,
    page: PropTypes.number.isRequired,
    rowsPerPage: PropTypes.number.isRequired,
};
  

const styles = {
    appBar: {
        backgroundColor: '#3D3F4F'
    }
}

const headCells = ["Id","Time Stamp", "Type", "Level", "Duration", "Detail"];

const typeOptions = [
    {label: "CREATE_RECEIPT", value: "CREATE_RECEIPT"},
    {label: "CREATE_CLEARANCE_TASK", value: "CREATE_CLEARANCE_TASK"},
    {label: "CREATE_TPM_TREE", value: "CREATE_TPM_TREE"},
    {label: "UPDATE_CLEARANCE_TASK", value: "UPDATE_CLEARANCE_TASK"},
    {label: "CREATE_CLEARANCE_RECORD", value: "CREATE_CLEARANCE_RECORD"},
    {label: "CREATE_MERKLE_PROOF", value: "CREATE_MERKLE_PROOF"},
    {label: "CREATE_VERIFICATION_PROOF", value: "CREATE_VERIFICATION_PROOF"},
]
const defaultTypeOptionsSet = [{label: "UPDATE_CLEARANCE_TASK", value: "UPDATE_CLEARANCE_TASK"}];
const defaultTypeOptions = ["UPDATE_CLEARANCE_TASK"];


const EventLogs = () => {
    const classes = useStyles1();
    const [data, setData] = useState([]);
    const [dataLength, setdataLength] = useState(0);

    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);

    const [startTime, setstartTime] = useState(moment().subtract(1, 'week'));
    const [endTime, setendTime] = useState(moment());
    const [typeValue, settypeValue] = useState(defaultTypeOptions)
    const [typeSet, settypeSet] = useState(defaultTypeOptionsSet);

    const handleSearch = () => {
        if(endTime > startTime) {
            fetchData();
        }
        else {
            alert("Start time can not be after end time!")
        }
    }
    const settype = (args)=>{
        const values = args.map((item)=>{
            return item.value;
        });
        _.pull(values, undefined);
        settypeValue(values);
        settypeSet(args);
    }

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = event => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const fetchData = () => {
        axios.post(`/ledger/eventLogs/${rowsPerPage}/${page}`, {
            startTime: startTime.format("x"),
            endTime: endTime.format("x"),
            typeSet: typeValue,
        }).then(res => {
            setData(res.data)
            setdataLength(Number(res.headers["pagination-count"]))
        })
    }
    useEffect(() => {
        fetchData()
    }, [rowsPerPage, page])

    return (
        <div>
            <Box pl={30} pr={2}>
                <Box fontSize={24} pt={12} color="white" px={2}>
                    Event Logs
                </Box>
                <Box pb={3}>
                    <div className="container-data-status">
                        <div className="row">
                            <div className="col-6">
                                <div className="container-data-status">
                                    <div className="row">
                                        <div className="col-6 data-status-text-color">
                                            Start time
                                        </div>
                                        <div className="col-6 data-status-text-color">
                                            End time
                                        </div>
                                        <div className="col-2">
                                        </div>
                                    </div>
                                    <div className="row mt-2">
                                        <div className="col-6">
                                            <Datetime
                                                onChange={val => setstartTime(val)}
                                                value={startTime}
                                            />
                                        </div>
                                        <div className="col-6">
                                            <Datetime
                                                onChange={val => setendTime(val)}
                                                value={endTime}
                                            />
                                        </div>
                                    </div>
                                </div>

                            </div>
                            <div className="col-6">
                                <div className="container-data-status">
                                    <div className="row">
                                        <div className="col-5 data-status-text-color">
                                            Type
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-8 pt-2">
                                            <MultiSelect
                                                options={typeOptions}
                                                value={typeSet}
                                                onChange={settype}
                                                labelledBy={"Select"}
                                            />
                                        </div>
                                        <div className="col-4 pt-2">
                                            <button className="btn btn-primary" onClick={handleSearch}>Search</button>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                        <div className="">
                            <div className="container-fluid error-alert">
                                {/* {this.renderAlert()} */}
                            </div>
                        </div>
                    </div>
                </Box>
                <Paper>
                    <TableContainer className={classes.container} component={Paper}>
                        <Table aria-label="sticky table">
                            <TableHead>
                                <TableRow>
                                    {headCells.map((headCell) => (
                                        <TableCell key={headCell}>
                                            {headCell}
                                        </TableCell>
                                    ))}
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {rowsPerPage > 0 &&
                                    data.map(row => (
                                    <TableRow key={row.id}>
                                        <TableCell style={styles.tabelCell}>
                                            {row.id}
                                        </TableCell>
                                        <TableCell data-toggle="tooltip" title={timeStamp2String(row.timestamp)} style={styles.tabelCell}>
                                            {row.timestamp}
                                        </TableCell>
                                        <TableCell style={styles.tabelCell}>
                                            {row.type}
                                        </TableCell>
                                        <TableCell style={styles.tabelCell}>
                                            {row.level}
                                        </TableCell>
                                        <TableCell style={styles.tabelCell}>
                                            {row.duration}
                                        </TableCell>
                                        <TableCell style={styles.tabelCell}>
                                            {row.detail}
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                            
                        <TablePagination
                            rowsPerPageOptions={[5, 10, 25, 100]}
                            colSpan={3}
                            count={dataLength}
                            component="div"
                            rowsPerPage={rowsPerPage}
                            page={page}
                            SelectProps={{
                                inputProps: { 'aria-label': 'rows per page' },
                                native: true,
                            }}
                            onChangePage={handleChangePage}
                            onChangeRowsPerPage={handleChangeRowsPerPage}
                            ActionsComponent={TablePaginationActions}
                        />
                                
                        
                </Paper>
            </Box>
        </div>
    )
}

export default EventLogs;
