import React, { Component } from 'react'
import axios from '../../sposerver_https'
import SpoInfoCard from '../SpoInfoCard/SpoInfoCard';
import _ from 'lodash'
import Hammer from '../image/hammer.svg';
import Ethereum from '../image/ethereum.svg';
import File from '../image/file.svg';
import Desktop from '../image/desktop.svg';

export default class SpoServerInfo extends Component {
    state = {
        data: null,
        serviceRecordInfoData: null,
        titles: {
            buildInfo: {
                icon: Hammer,
                title: "buildInfo"
            },
            ethereumInfo: {
                icon: Ethereum,
                title: "ethereumInfo"
            },
            serviceRecordInfo: {
                icon: File,
                title: "serviceRecordInfo"
            },
            serverInfo: {
                icon: Desktop,
                title: "serverInfo"
            }
        },
        cards: ["buildInfo", "ethereumInfo"]
    };
    
    componentDidMount() {
        axios.get('/page/indexInfo').then(res => {
            const result= _.mapValues(res.data,(value) => {
                return this.transformJsonToArray(value);
            })
            this.setState({data:result});
        });
    }
    transformJsonToArray = (valueJsonObj) => {
        const arr = _.map(valueJsonObj, (value, key) => {
                const valueObj = {text:"", value:"", valueType:""};
                valueObj.text = key;
                valueObj.value = value;
                return valueObj;
        });
        return arr;
    }
    renderBuildInfo() {
        if(this.state.data) {
            const cardTitle = this.state.titles.buildInfo;
            const cardInfo = this.state.data.buildInfo;
            return <SpoInfoCard title={cardTitle} information={cardInfo} />
        }
    }
    renderEthereumInfo() {
        if(this.state.data) {
            const cardTitle = this.state.titles.ethereumInfo;
            const cardInfo = this.state.data.ethereumInfo;
            return <SpoInfoCard title={cardTitle} information={cardInfo} />
        }
    }
    renderServerInfo() {
        if(this.state.data) {
            const cardTitle = this.state.titles.serverInfo;
            const cardInfo = this.state.data.serverInfo;
            return <SpoInfoCard title={cardTitle} information={cardInfo} />
        }
    }
    renderServiceRecordInfo() {
        if(this.state.data) {
            const cardTitle = this.state.titles.serviceRecordInfo;
            const cardInfo = this.state.data.serviceRecordInfo;
            return <SpoInfoCard title={cardTitle} information={cardInfo} />
        }
    }
    renderCard(cardId) {
        const cardTitle = this.state.titles[cardId];
        const cardInfo = this.state.data[cardId];
        return <SpoInfoCard title={cardTitle} information={cardInfo} />
    }

    renderData() {
        if(this.state.data) {
            const result = _.map(this.state.cards, (cardId) => {
                const card = this.renderCard(cardId);
                return card;
            })
            return result;
        }
    }

    render() {
        return (
            <div className="container-main">
                <div className="d-sm-flex align-items-center justify-content-between mb-4">
                    <h3 className="mb-0 text-white">Overview</h3>
                </div>
                <div className="row">
                    <div className="col-xl-12 col-lg-12 col-md-12">
                        {this.renderServerInfo()}
                    </div>
                </div>
                <div className="row">
                    <div className="col-xl-6 col-lg-6 col-md-6">
                        {this.renderData()}
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-6">
                        {this.renderServiceRecordInfo()}
                    </div>
                </div>
                {/* <div className="col-xl-6 col-lg-6 col-md-6">
                    {this.renderEthereumInfo()}
                </div> */}
            </div>
        )
    }
}
